.menu-list {
    width: 224px;

    .menu-item {
        margin-left: 0;
    }
}

.menu-poper {
    padding-bottom: 8px;
}

.header {
    position: relative;
    height: 50px;
    margin-top: -8px;
    cursor: pointer;
}

.back-btn {
    width: 50px;
    height: 100%;
    background: transparent;
    cursor: pointer;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.menu-item {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    font-weight: 600;

    &.sperate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }

    &:hover {
        background: rgba(22, 24, 35, 0.03);
    }
}
